}

.SearchBar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 50px;
  border: 1px;
  border-bottom: 3px;
}

.SearchBar input {
  border: none;
  background-color: white;
  width: 800px;
  font-family: "Bebas Neue", sans-serif;
  font-weight: 700;
  color: black;
  font-size: 30px;
  height: 60px;
  padding-left: 10px;
  border: 0;
  outline: 0;
  border-bottom: 4px solid #94ae89;
}

:focus {
  outline: none !important;
}

::-webkit-input-placeholder {
  color: black;
  opacity: 1;
}

::-moz-placeholder {
  color: black;
  opacity: 1;
}

:-ms-input-placeholder {
  color: black;
  opacity: 1;
}

::-ms-input-placeholder {
  color: black;
  opacity: 1;
}

::placeholder {
  color: black;
  opacity: 1;
}

.SearchBar button {
  background-color: #94ae89;
  border: 2px;
  border-color: black;
  color: #23272a;
  font-family: "Bebas Neue", sans-serif;
  font-weight: 700;
  font-size: 25px;
  width: 110px;
}

.noSelect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.SearchResults {
  display: flex;
  flex-wrap: wrap;
  margin-top: 1rem;
  /* width: 500px;
  margin-left: -00px; */
}

.react-tabs {
  -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {
  margin: 0 0 10px;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.react-tabs__tab {
  border-radius: 4px 4px 0 0;
  background: #94ae89;
  bottom: -1px;
  margin: 0px 2px 0px 2px;
  list-style: none;
  padding: 6px 12px;
  cursor: pointer;
  height: 30px;
  width: 283px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Bebas Neue", sans-serif;
  font-weight: 700;
  font-size: 20px;
}

.react-tabs__tab--selected {
  background: white;
  color: black;
  height: 30px;
  width: 283px;
}

.react-tabs__tab:focus {
  box-shadow: 0 0 5px hsl(208, 99%, 50%);
  border-color: hsl(208, 99%, 50%);
  outline: none;
}

.react-tabs__tab:focus:after {
  content: "";
  position: absolute;
  height: 5px;
  left: -4px;
  right: -4px;
  bottom: -5px;
  background: white;
}

.react-tabs__tab-panel {
  display: none;
  background-color: #94ae89;
  margin-top: -10px;
  margin-right: 2px;
  margin-left: 2px;
  height: 400px;
}

.react-tabs__tab-panel--selected {
  display: block;
  background-color: white;
  margin-top: -10px;
  margin-right: 2px;
  margin-left: 2px;
}

